@import url(https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css);
body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: rgba(255, 255, 255, 0.4)!important;
	background-blend-mode: lighten;
}

.background {
	background: url("https://expertwebinarseries.com/wp-content/uploads/2019/11/woman-in-restaurant-wearing-apron-1739748-1800x1200.jpg")no-repeat center center fixed;
	background-size: cover;
	min-height: 100vh;
	display: flex;
	background-color: rgba(255, 255, 255, 0.4)!important;
	background-blend-mode: lighten;
	flex-direction: column;
	align-items: center;
}

#nav_user {
	color: white;
}

.top-nav-collapse {
	background-color: #24355C;
}

.main-container {
	padding: 2%;
}

.main-container .card {
	min-height: 80vh;
	min-width: 90vw;
}

.main-container .row {
	margin-left: auto;
	margin-right: auto;
}

.white-txt {
	color: white;
	display: inline;
}

.card {
	margin-top: 5px;
	background: #eee;
	padding: 1em;
	min-width: 100%;
	overflow: scroll;
}

.card-close {
	cursor: pointer;
}

img.preview {
	max-width: 250px;
	max-height: 250px;
}

.table {
	table-layout: fixed;
	overflow: scroll;
	width: 100%;
}

.table th {
	font-size: .9rem;
	font-weight: 400;
}

.table td {
	font-size: .9rem;
	font-weight: 300;
}

.table-container .row {
	min-width: 100%;
}

.table-container .row div {
	font-size: .9rem;
	margin: 0px;
	padding: 1%;
}


/* change navbar background on collapse */

@media (max-width: 768px) {
	.white-txt {
		display: none;
	}
	.collapse {
		background: rgba(0, 0, 0, 0.8);
		padding-left: 15px;
		z-index: 999;
	}
	.card {
		margin-top: 20px;
		background: #eee;
	}
	.table th,
	.table .btn {
		font-size: .5rem;
	}
	.table td {
		font-size: .4rem;
	}
	.table-container .row div {
		font-size: .3rem;
	}
}

.table-container {
	overflow: scroll;
	margin: 0px;
  padding: 0px;
  border-radius: .2em ;
}

.table-header div {
	text-align: left;
	font-weight: bold;
	background-color: rgb(233, 236, 239);
	border: 1px solid rgb(222, 226, 230);
	color: rgb(94, 100, 107);
}

.table-row div {
	text-align: left;
	border: 1px solid rgb(222, 226, 230);
	color: rgb(94, 100, 107);
}

.recipe-details div {
	border: 0px;
	margin: 0px;
	padding: 0px;
	text-align: right;
	padding-right: 2%;
}

.fileContainer {
  overflow: hidden;
  position: relative;
}

.fileContainer [type=file] {
  cursor: inherit;
  display: block;
  font-size: 999px;
  filter: alpha(opacity=0);
  min-height: 100%;
  min-width: 100%;
  opacity: 0;
  position: absolute;
  right: 0;
  text-align: right;
  top: 0;
}

.fileContainer {
  background: rgb(60, 89, 148);
  border-radius: .5em;
  float: left;
  padding: .5em;
  margin:.3em;
  margin-left:0px;
  color :white;
}

.fileContainer [type=file] {
  cursor: pointer;
}
.form-wrapper {

  margin: 0 auto;
}
.App {
  text-align: center;
  margin-top: 30px;
}
nav {
  /** width: 300px; **/
  margin: 0 auto;
  background: #282c34;
  height: 70px;
  line-height: 70px;
}
nav .dropdown
{
  line-height: normal;
  border:3px solid red;
  margin:0px;
  display:block;
}
nav ul li {
  display: inline;
  list-style-type: none;
  text-align: center;
  padding: 30px;
}
nav ul li a {
  margin: 50px 0;
  font-weight: bold;
  color: white;
  text-decoration: none;
}
nav ul li a:hover {
  color: white;
  text-decoration: none;
}
.App-link {
  color: #61dafb;
}
@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.um-main-body {
  height:100%;
}
.Home {
	text-align: center;
}

.home-background {
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.intro {
	padding: 10px;
	background-color: rgba(0, 0, 0, 0.5);
	color: white;
	margin-top: -2em;
}

.Home-logo {
	padding: 10px;
	background-color: rgba(0, 0, 0, 0.5);
	-webkit-animation: Home-logo-spin infinite 20s linear;
	        animation: Home-logo-spin infinite 20s linear;
	height: 40vmin;
	pointer-events: none;
	border-radius: 40px;
}

@-webkit-keyframes Home-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes Home-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
img {
	max-width: 100%;
}

div.recipe {
	margin: 30px;
}

.tab-content {
	overflow: hidden;
}

.tab-content img {
	width: 100%;
	-webkit-animation-name: opacity;
	animation-name: opacity;
	-webkit-animation-duration: .3s;
	animation-duration: .3s;
}

@media screen and (min-width: 997px) {
	.wrapper {
		display: flex;
	}
}

.details {
	display: flex;
	flex-direction: column;
}

.recipe-title,
.rating,
.recipe-description,
.submitted-by {
	margin-bottom: 15px;
}

.recipe-title {
	margin-top: 0;
}

.star {
	color: orange;
}
